import React from 'react';
import Helmet from 'react-helmet';
import { useStaticQuery, graphql } from 'gatsby';

interface IMeta {
    name: string;
    content: string;
}

interface ISEOProps {
    description?: string;
    lang?: string;
    meta?: IMeta[];
    title?: string;
}

export function SEO({
    description,
    lang = 'en',
    meta = [],
    title
}: ISEOProps) {
    const { site } = useStaticQuery(
        graphql`
            query {
                site {
                    siteMetadata {
                        title
                        description
                        author
                    }
                }
            }
        `
    );

    const metaAuthor = site.siteMetadata.author;
    const metaDescription = description || site.siteMetadata.description;
    const metaTitle = title || site.siteMetadata.title;

    return (
        <Helmet
            htmlAttributes={{ lang }}
            title={metaTitle}
            meta={[
                {
                    name: `description`,
                    content: metaDescription
                },
                {
                    property: `og:title`,
                    content: metaTitle
                },
                {
                    property: `og:description`,
                    content: metaDescription
                },
                {
                    property: `og:type`,
                    content: `website`
                },
                {
                    name: `twitter:card`,
                    content: `summary`
                },
                {
                    name: `twitter:creator`,
                    content: metaAuthor
                },
                {
                    name: `twitter:title`,
                    content: metaTitle
                },
                {
                    name: `twitter:description`,
                    content: metaDescription
                }
            ].concat(meta)}
        />
    );
}
